<template>
  <ul>
    <li :class="{bold: isFolder, tree: isFolder}" @click="select">
      <span v-if="isFolder" @click="toggle">
        [{{open ? '-' : '+'}}]
      </span>
      {{model.data.name}}
      <button v-if="!isFolder" @click='delete'>删除</button>
    </li>
    <div v-show='open' v-for='model in model.children'>
      <tree :model='model'></tree>
    </div>
  </ul>
</template>

<script>
import Tree from './Tree'

export default {
  name: 'tree',
  data () {
    return {
      open: false
    }
  },
  props: ['model'],
  computed: {
    isFolder: function () {
      return this.model.size > 0
    }
  },
  methods: {
    select () {
      this.$dispatch('selected', this.model)
    },
    toggle () {
      if (this.isFolder) {
        this.open = !this.open
        if (this.open) {
          this.model.loadChild()
        }
      }
    },
    delete () {
      alert('是否确信要删除该节点？')
      this.model.delete()
    },
    addChild () {
      this.model.children.push({
        name: 'new stuff'
      })
    }
  },
  components: {
    Tree
  }
}
</script>

<style>
body {
  font-family: Menlo, Consolas, monospace;
  color: #444;
}
.tree {
  cursor: pointer;
}
.bold {
  font-weight: bold;
}
ul {
  padding-left: 1em;
  line-height: 1.5em;
  list-style-type: dot;
}
</style>



